import React from 'react';
import connect from '@vkontakte/vk-connect';
import Icon24Gift from '@vkontakte/icons/dist/24/gift';
import Icon24VkLogo from '@vkontakte/icons/dist/24/logo_vk';
import Icon24Money from '@vkontakte/icons/dist/24/money_circle';

import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';


import {Panel, Alert, Group, PanelHeader, Gallery, Div, Button} from "@vkontakte/vkui"


class Market extends React.Component {

    state = {
        showImg: false
    };

    showImg = () => {
        this.setState({showImg: true});
    };
    buyItem(el) {
      console.log(el.id);
    }
    async sendPay() {
      console.log("dfvdfv");
      const pays = await connect.send("VKWebAppOpenPayForm", {"app_id": 7150912, "action": "pay-to-user", "params": { "user_id": 193790619, "amount": 100}});

    }

    async serverGet(){
      const result = await fetch('https://demo146.bravo.vkhackathon.com/api/v1/challangeContainer/getSportChallenges', {
      method: "GET",
      // body: JSON.stringify(''),
    }).then(res => res.json())
    .then(json => console.log(json));
    }
    openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: 'Нет',
                    autoclose: true,
                    style: 'cancel',
                }, {
                    title: 'Да',
                    autoclose: true,
                    action: this.showImg
                }]}
                onClose={() => this.props.closePopout()}
            >
                <h2>Вопрос значит</h2>
                <p>Вас роняли в детстве?</p>
            </Alert>
        );
    }

    render () {
        const {id} = this.props;
          return (
              <Panel id={id}>
                <PanelHeader>Магазин</PanelHeader>
                <Div  align="center">
                  <Button before={<Icon24VkLogo/>} className="storeButton1" onClick={e => this.sendPay()}>VK PAY</Button>
                  <Button before={<Icon24Gift/>} onClick={e => this.serverGet()} className="storeButton1">Бонусы</Button>
                  <Button before={<Icon24Money/>} onClick={e => this.serverGet()} id="storeButton2" level="outline">10.00</Button>
                </Div>
                <Group >
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div className="divImage" />
                  </Gallery>
                </Group>
                <Group>
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div className="divImage1"/>
                  </Gallery>
                </Group>
                <Group>
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div className="divImage2"/>
                  </Gallery>
                </Group>
                <Group>
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div className="divImage3"/>
                  </Gallery>
                </Group>
                <Group>
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div className="divImage4"/>
                  </Gallery>
                </Group>
                <Group>
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div className="divImage5"/>
                  </Gallery>
                </Group>
                <Group>
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div className="divImage6"/>
                  </Gallery>
                </Group>
                <Group>
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div className="divImage7"/>
                  </Gallery>
                </Group>
              </Panel>
          )
        }
      }


const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default Market;
